.slider__toggles {
  display: flex;
  align-items: center;
  justify-content: center;
}

.slider__toggle {
  background-color: $main-color-30;

  margin: 0 10px;
  padding: 0;

  width: 6px;
  height: 6px;
  border: none;
  border-radius: 50%;

  &--active {
    width: 12px;
    height: 12px;

    margin: 0 7px;

    background-color: #fff;
    border: 3px solid $main-color;
  }
}
